body {
  min-height: 100vh;
}
.text-m {
  font-weight: 500;
}
.text-b {
  font-weight: 700;
}
.text-v-middle {
  vertical-align: middle;
  line-height: 100%;
}
.modal {
  width: 70%;
}
.navbar {
  // padding-left: 2em;
  // padding-right: 2em;
}
.info-bar {
  &_item {
    display: flex;
    justify-content: flex-end;
  }
  & > .progress {
    margin-top: 2em;
    padding-top: 12px;
    border-radius: 5px;
  }
}
.progress-text {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 0;
  z-index: 2;
  line-height: 1em;
  font-size: 0.75em;
}

.home {
  &_top {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &_top_left {
    max-width: 55%;
    width: 100%;
  }
  &_top_right {
    max-width: 40%;
    width: 100%;
  }
  &_chart {
    width: 100%;
  }
}

.tasks, .history {
  &_item {
    position: relative;
    line-height: 2.5em !important;
    &:hover {
      transition: .25s;
      background-color: rgba(204, 204, 204, 0.6);
    }
  }
  &_item_title {
    font-weight: 500;
  }
  &_item_reward {
    margin-bottom: 0;
    margin-top: 0;
  }
  &_actions {
    
  }
}

.tasks {
  &_item.once::before {
    content: 'x1';
    display: block;
    width: 1.75em;
    height: 1.75em;
    background-color: #116bc5;
    color: rgb(255, 255, 255);
    font-size: 0.7em;
    font-weight: 500;
    line-height: 1.75em;
    text-align: center;
    position: absolute;
    left: 0;
    top: 0;
    border-radius: 50%;
  }
  &_item_skills {
    color: rgba(0, 0, 0, 0.35);
  }
  &_item:hover &_item_skills {
    transition: .25s;
    color: inherit;
  }
}

.history {
  & &_item {
    background-color: #acecbe94;
    transition: .25s;
    &:hover {
      background-color: #acecbe;
    }
  }
}

.skills {
  &_item {
    line-height: 2.5em !important;
    &:hover {
      transition: .25s;
      background-color: rgba(204, 204, 204, 0.6);
    }
  }
  &_item_title {
    // font-weight: 700;
  }
  &_info_box > .progress {
    margin-top: 0;
    margin-bottom: 0;
    padding-top: 12px;
    border-radius: 5px;
  }
}

.chars {
  &_item {
    line-height: 2.5em !important;
    &:hover {
      transition: .25s;
      background-color: rgba(204, 204, 204, 0.6);
    }
  }
  &_item_title {
    font-weight: 500;
  }
  &_info_box > .progress {
    margin-top: 0;
    margin-bottom: 0;
    padding-top: 12px;
    border-radius: 5px;
  }
}

#newTaskBtn {}

.btn-floating {
  position: fixed;
  right: 2rem;
  bottom: 2rem;
}

#newTask {
  & > .modal-content {
    padding-bottom: 15%;
  }
  & > .modal-footer {

  }
}

#newSkill {
  & > .collection {
    
  }
  & .collection-item {
    line-height: 2.5rem !important;
    & > .row {
      margin-bottom: 0;
    }
  }
  & .collection-item .input-field {
    margin-top: 0;
    margin-bottom: 0;
  }
  & .collection-item .input-field > input {
    margin-bottom: 0;
    height: auto;
    & + label:not(.active) {
      transform: translateY(0);
    }
  }
  & .collection-item .input-field > label {
    margin-bottom: 0;
    height: auto;
  }
}

.task_form {
  &_reward {

  }
  &_reward .material-icons {
    font-size: 34px;
  }
  &_reward .task_form_num_input {
    height: 2.5rem !important;
  }
}

.shop, .inventory, .achievements {
  &_list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: stretch;
    &::after {
      content: '';
      flex: 0 0 30%;
    }

  }
  &_item {
    flex: 0 0 30%;
    padding: 0.7em 1.2em;
    text-align: center;
    position: relative;
    & > .row {
      margin-bottom: 0;
    }
  }
  &_item_title {
    font-weight: 700;
    font-size: 1.2em;
    max-width: 68%;
    margin: 0 auto;
  }
  &_item_desc {
    margin-bottom: 1em;
  }
  &_item_cost {
    font-weight: 500;
    font-size: 1.2em;
  }
  &_item_btn {}
  &_item_more {
    position: absolute;
    right: 0;
    top: 0;
  }
  &_item_actions {
    & > li {
      height: auto;
      min-height: auto;
      // padding-bottom: 0;
    }
  }
  &_item_action_btn {
    width: 100%;
    height: 100%;
    text-align: center;
    margin-bottom: 0;
  }
}

.achievements {
  &_item {
    &:not(.success) {
      opacity: .55;
      transition: .25s;
      background-color: rgb(226, 226, 226);
      &:hover {
        opacity: 1;
        background-color: rgb(243, 243, 243);
      }
    }
    &.success {
      background-color: rgba(0, 255, 71, 0.450);
    }
  }
  &_more {
    // position: relative;
  }
  &_more_icon {
    border-radius: 0 3px 50% 3px;
    font-weight: 700;
    // border-top-left-radius: 0;
    width: 1.5rem;
    height: 1.5rem;
    line-height: 1.5em;

    border: 1px solid #ccc;
    background-color: rgba(204, 204, 204, 0.6);
    position: absolute;
    left: 0;
    top: 0;
  }
  &_more_icon:hover + &_more_list, &_more_list:hover {
    display: block;
  }
  &_more_list {
    display: none;
    border-radius: 7px;
    width: 75%;
    text-align: left;
    list-style-position: inside;
    font-size: 0.8em;
    line-height: 1.2em;
    position: absolute;
    left: -20%;
    top: 1.5rem;
    margin-top: 0;
    padding: 5px 6px;
    background-color: rgb(233, 233, 233);
    & > li::before {
      content: '- ';
      font-weight: 700;
    }
  }
}

.achievement_conditions {
  padding-top: 2em !important;
  &_item {
    margin-bottom: 0;
  }
  &_item:not(:first-of-type) {
    border-top: 2px solid rgb(179, 176, 176);
    padding-top: 12px;
  }
}

.achievement_form_reward {
  display: flex;
  align-items: center;
  &_meta {
    // height: 100%;
    // vertical-align: middle;
    // display: flex;
    // flex-direction: column;
    // justify-content: center;
    // align-items: center;
  }
  &_inputs {
    margin-left: 0;
    margin-bottom: 0;
  }
}
